.post-type-dp_intro_tours {

	.acf-label--with-info-icon {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}


	.acf-label {
		.description {
			div {
				height: 5px;
			}
		}
	}

	#titlewrap {

		#title-prompt-text {
			@include atXXS {
				font-size: 14px;
				white-space: nowrap;
			}

		}
	}

	#post-body {
		.acf-postbox {

			&.dp-acf-tippy {

				&>.acf-fields {

					padding: 10px 20px !important;

					.acf-field {

						//grid-column: 1 / span 6; // default
						&.acf-field-select {
							.select2-container {
								.select2-selection {
									&:focus {
										outline: none !important;
										border: rgba(var(--dpu-color-2), 1) solid 1px !important;
									}

									&__rendered {
										color: $text-color-dark !important;
										font-weight: 500 !important;
									}
								}

							}
						}

						&.acf-field-relationship {
							.acf-relationship {
								.list {
									.acf-rel-item:hover {
										background: rgba(var(--dpu-color-3), 0.89) !important;
										color: rgba(var(--dpu-color-3-text), 1) !important;
									}
								}

								.values .acf-rel-item .acf-icon[data-name="remove_item"] {
									top: 3px;
								}

								.selection .values .acf-rel-item {
									cursor: default !important;
								}
							}
						}

						&.acf-field-text {
							.acf-input {
								input {
									color: $text-color-dark !important;
									font-weight: 500 !important;

									&::placeholder {
										font-weight: 400 !important;
										color: $text-color-placeholder;
									}

									&:focus {
										outline: none !important;
										border: rgba(var(--dpu-color-2), 1) solid 1px !important;
									}
								}
							}
						}

						&.acf-field-true-false {
							&:not(.acf-field--main) {
								display: flex;
								flex-direction: row-reverse;
								align-items: center;
								justify-content: flex-end;


								.acf-label {
									flex-basis: 100%;
									margin: 0 0 0 10px;
									margin-bottom: 0;

									label {
										margin-bottom: 0;
									}
								}
							}

							.acf-input {
								flex-shrink: 0;

								.acf-true-false {

									.acf-switch {
										transition: all 0.3s ease-in-out;
										border: rgba(var(--dpu-color-3), 0.95) solid 1px !important;
										background: rgba(var(--dpu-color-3), 0.05) !important;

										&.-on {
											background: rgba(var(--dpu-color-3), 0.95) !important;

											&:hover {
												background: rgba(var(--dpu-color-3), 0.75) !important;
											}

											.acf-switch-on {
												text-shadow: unset !important;
											}

										}

										.acf-switch-on {
											color: rgba(var(--dpu-color-3-text), 1) !important;
											font-weight: 500 !important;
										}

										.acf-switch-off {
											color: $text-color-dark !important;
											font-weight: 500 !important;
										}

										.acf-switch-slider {
											border-color: rgba(var(--dpu-color-3), 1) !important;
										}
									}
								}
							}

						}

						&.acf-field-group {
							display: flex;
							flex-direction: column;
							border: none !important;

							>.acf-label {
								>label {
									font-size: 18px;
									text-transform: uppercase;
								}
							}
						}

						&--main {
							>.acf-label {
								>label {
									font-size: 18px;
									text-transform: uppercase;
								}
							}
						}


						&:not(.acf-field--no-info-icon):not(.acf-field-group) {
							.acf-label {
								.description {
									visibility: hidden;
								}
							}

							span.message {
								visibility: hidden;
							}
						}

						// touch devices
						@media (hover: none) {

							.acf-rel-item {
								min-height: 25px !important;
								font-size: 15px;
								padding-top: 10px !important;
								padding-bottom: 6px !important;
							}

							.acf-icon {
								&--s {
									width: 36px;
									height: 36px;
									line-height: 36px;
									font-size: 26px;
								}
							}

							.acf-relationship .values .acf-rel-item .acf-icon[data-name="remove_item"] {
								display: inline-block;
								background: rgba(150, 101, 45, 0.841);
								width: 30px;
								height: 30px;
								line-height: 30px;
								font-size: 20px;
								top: 4px;
							}


							.select2-container {
								font-size: 18px;

								.select2-selection:not(.select2-selection--multiple) {

									min-height: 40px;

									.select2-selection__rendered {
										height: 40px;
										line-height: 40px !important;

									}

									.select2-selection__arrow {
										height: 40px !important;

										b {
											border-width: 7px 5px 0 5px !important;
											left: 30% !important;
											border-color: rgba(var(--dpu-color-2), 1) transparent transparent transparent !important;
										}
									}
								}

								.select2-selection.select2-selection--multiple {
									.select2-selection__choice {
										.select2-selection__choice__remove {
											font-size: 30px;
											line-height: 1;

										}

										.acf-selection {}

										.select2-results__option {
											padding: 12px;
										}
									}
								}
							}


						}
					}
				}


				// responsive
				&>.acf-fields {
					display: grid;
					grid-template-columns: 16.666% 16.666% 16.666% 16.666% 16.666% 16.666%;
					grid-template-rows: auto auto auto auto auto auto;
					row-gap: 20px;

					@include atL {
						display: block;
					}

					@include atXXS {
						padding: 10px 15px !important;
					}

					.acf-field {

						&.acf-field-table {
							@include atXXS {
								padding: 15px 0;
							}
						}

						&.acf-field-radio {
							@include atS {
								.acf-radio-list {
									li {
										display: block;
										float: none;
										padding-bottom: 10px;
									}
								}
							}
						}

						&.acf-field--main {
							grid-column: 1 / span 6;
							border: none !important;

							.acf-input {
								.acf-switch span {
									font-size: 15px;
								}
							}

							&[data-name="intro_enabled"] {
								grid-column: 1 / span 1;
								min-width: 200px;
							}

							&[data-name="add_additional_trigger"] {
								grid-column: 4 / span 3;
								grid-row: 3 / span 1;
								max-height: 150px;
							}
						}



						&[data-name="intro_steps"] {
							grid-column: 1 / span 6;

							.acf-label {
								margin-bottom: 0;
							}
						}

						&[data-name="intro_related_posts_multipage"] {
							grid-column: 1 / span 6;
						}

						&[data-name="plugin_version"] {
							grid-column: 1 / span 2;
						}

						&.acf-field-group {
							&[data-name="intro_design"] {
								grid-column: 1 / span 2;
							}

							&[data-name="dp_tour_behaviour"] {
								grid-column: 3 / span 4;

								.acf-fields {
									display: grid;
									grid-template-columns: 50% 50%;

									@include atL {
										display: block;
									}
								}

							}

							&[data-name="intro_labeling"] {
								grid-column: 1 / span 3;
							}

							&[data-name="intro_url_variables"] {
								grid-column: 4 / span 3;

								.acf-field[data-name="url_vars_examples"] textarea {
									height: 105px;
									cursor: pointer;

									&.url-vars-disabled {
										pointer-events: none;
										opacity: 0.6;
									}
								}
							}

							&[data-name="intro_trigger"] {
								grid-column: 1 / span 3;
								grid-row: 3 / span 1;
							}

							&[data-name="intro_trigger_2"] {
								top: 100px;
								padding-bottom: 100px;
								grid-column: 4 / span 3;
								grid-row: 3 / span 1;

								@include atL {
									top: 0;
									padding-bottom: 15px;
								}
							}
						}
					}
				}

				&.columns-1 {
					.acf-postbox>.acf-fields {
						@include atS {
							display: block;
						}


						.acf-field {
							&.acf-field-group {
								@include atS {
									&[data-name="intro_trigger_2"] {
										top: 0;
										padding-bottom: 0;
									}

									&[data-name="dp_tour_behaviour"] {
										.acf-fields {
											display: block;
										}
									}
								}
							}
						}
					}
				}

			}
		}

	}

}