.dpit-admin-action {
	padding-top: 15px;

	&__btn {
		margin-bottom: 10px !important;
	}

	&--top {
		min-width: 400px;

		.dpit-admin-action__btn {
			margin-bottom: 0px !important;
		}

		@include atS {
			min-width: unset;
		}
	}
}

.dp-or-set-manually {
	padding: 8px 0;
	margin-top: 20px;
	font-variant: small-caps;
	font-weight: 600;
	font-size: 15px;
}


@keyframes pulse {
	0% {
		transform: scale(0.97);
		box-shadow: 0 0 0 0 rgba(var(--dpu-color-3), 0.7);
	}

	70% {
		transform: scale(1);
		box-shadow: 0 0 0 10px rgba(var(--dpu-color-3), 0);
	}

	100% {
		transform: scale(0.97);
		box-shadow: 0 0 0 0 rgba(var(--dpu-color-3), 0);
	}
}

.dp-visual-edited-acf {
	animation: pulse 1s;
}

.dp-highlighted-el {
	background-color: rgba(red, 0.2) !important;
}