.post-type-dp_intro_tours {

	.acf-table-wrap,
	.acf-table-wrap *,
	.acf-table-wrap *:before,
	.acf-table-wrap *:after {
		box-sizing: border-box;
	}


	.acf-table-wrap {
		&--cs {

			.acf-table-body-cont {
				@import './../../../../includes/assets/css/editor-content-color';
				@import './../../../../includes/assets/css/editor-content';
			}
		}
	}

	/* TABLE { */

	.acf-table-table {
		display: table;
		border-collapse: collapse;
		width: 100%;

		.acf-icon {
			display: flex;
			align-items: center;
			justify-content: center;

			&--s,
			&--xs {
				width: 22px;
				height: 22px;
				line-height: 22px;
				font-size: 13px;
			}

			&--xxs {
				width: 18px;
				height: 18px;
				line-height: 18px;
				font-size: 10px;
			}
		}
	}

	.acf-table-body-row {
		width: 100%;
	}

	.acf-table-table * {
		background-clip: padding-box;
		/* fix FireFox and IE background color painting over the borders */
	}

	/* TOP { */

	.acf-table-top-row {
		position: relative;
		display: table-row;
	}

	.acf-table-top-left {
		position: relative;
		display: table-cell;
		width: 1px;
		padding: 4px 8px 4px 16px;
		min-height: 27px;
	}

	.acf-table-top-cell {
		position: relative;
		display: table-cell;
		padding: 4px 8px;
		border: 1px solid #e1e1e1;
		background-color: #eee;
		cursor: move;
		min-width: 68px;
	}

	.acf-table-top-right {
		position: relative;
		display: table-cell;
		width: 26px;
	}

	.acf-table-top-cont {
		margin: 0;
		padding: 0;
		font-size: inherit;
		line-height: inherit;
		text-align: center;
		color: #aaa;
	}

	/* TOP } */

	/* HEADER { */

	.acf-table-header-row {
		position: relative;
		display: table-row;
	}

	.acf-table-header-left {
		position: relative;
		display: table-cell;
	}

	.acf-table-header-cont {
		margin: 0;
		padding: 0;
		font-size: inherit;
		line-height: inherit;
		font-weight: bold;
	}

	.acf-table-header-cell {
		position: relative;
		display: table-cell;
		text-align: center;
		padding: 4px 8px;
		border: 1px solid #e1e1e1;
		background-color: rgba(var(--dpu-color-3), 0.6);
		//color: rgba(var(--dpu-color-3-text), 1);
		color: rgba(var(--dpu-color-1), 0.9);
		height: 28px;

		&--with-info-icon {

			padding-right: 20px;

			.acf-table-header-cont {
				margin-top: 15px;
				margin-bottom: 10px;
			}
		}
	}

	.acf-table-header-right {
		position: relative;
		display: table-cell;
		width: 26px;
	}


	/* HEADER } */

	/* BODY { */

	.acf-table-body-row {
		position: relative;
		display: table-row;
	}

	.acf-table-body-left {
		position: relative;
		display: table-cell;
		border: 1px solid #e1e1e1;
		padding: 4px 8px 4px 8px;
		background-color: rgba(var(--dpu-color-3), 0.65);

		height: 27px;
		min-width: 40px;
		cursor: ns-resize;
		text-align: right;
		vertical-align: middle;

		.acf-table-body-left-wrap {
			display: flex;
			justify-content: center;

			.acf-table-body-cont {
				padding-right: 5px;
				padding-left: 5px;
				font-weight: 700;
				font-size: 16px;
				color: rgba(var(--dpu-color-1), 0.9);
				line-height: 1;
			}

			.acf-table-sort-row-icon {
				width: 8px;
				transition: all 0.3s ease-in-out;
				height: auto;

				svg {
					path {
						transition: fill 0.3s ease-in-out;
						fill: rgba(var(--dpu-color-2), 0.85);

					}
				}

			}
		}

		&:hover {
			.acf-table-body-left-wrap {
				.acf-table-sort-row-icon {
					width: 8px;

					svg {
						path {
							fill: rgba(var(--dpu-color-1), 1);
						}
					}

				}
			}
		}

	}

	.acf-table-body-cell {
		position: relative;
		display: table-cell;
		border: 1px solid #e1e1e1;
		padding: 4px 8px;
		background-color: white;

		@include atXXS {
			max-width: calc(90vw - 30px);
		}

		&:not(.acf-table-body-cell--text_wp_edit) {
			text-align: center;
		}

		.acf-table-body-cont {
			@include atTouchDev {
				min-height: 35px !important;
			}
		}

		&.acf-table-body-cell--text {
			cursor: pointer;
		}

		&.acf-table-body-cell--text_wp_edit {
			cursor: pointer;


			.acf-table-body-cont {
				min-width: 300px;
				user-select: none;

				@include atXXS {
					min-width: 300px;
				}

				pre {
					white-space: pre-wrap;
				}

			}
		}

		&.acf-table-body-cell--link {
			.acf-table-body-cont {
				min-width: 100px;

				@include atXXS {
					min-width: 100px;
				}


			}
		}

	}

	.acf-table-body-right {
		position: relative;
		display: table-cell;
		border-bottom: 1px solid transparent;
		min-width: 30px;
	}

	.acf-table-body-cont {
		margin: 0;
		padding: 0;
		font-size: inherit;
		line-height: inherit;
		white-space: pre-wrap;

		&--rt {
			white-space: normal;
		}


		&.hidden-for-edit {
			opacity: 0;
			height: 0;
		}
	}

	.acf-table-body-cont-li {
		display: flex;
		align-items: center;

		.acf-icon {
			flex-basis: 18px;
			margin-left: 5px;
		}

		span {
			flex-basis: calc(100% - 23px);
		}

		margin-bottom: 10px;
	}

	.acf-table-cell-ec {
		margin: 10px -4px 0;
		padding: 10px 8px 10px 5px;
		background-color: rgba(var(--dpu-color-3), 0.15);
		border-radius: 2px;
		text-align: left;
		display: flex;
		flex-direction: column;
		align-items: flex-start;

		.acf-cell-eci-wrap {
			display: flex;
			align-items: center;

			&[data-id="iterate_after_click_delay_ms"] {
				flex-direction: column-reverse;
			}

			&[data-id="iterate_after_hover_delay_ms"] {
				flex-direction: column-reverse;
			}

			span {
				font-variant: small-caps;
				margin-bottom: 0.2em;
				white-space: nowrap;
			}

			padding-bottom: 5px;
			margin-bottom: 5px;
		}

		.acf-table-body-cont-sub {
			margin-top: 0;
		}

		&--hidden {
			opacity: 0;
			position: absolute;
			pointer-events: none;
		}
	}

	/* BODY } */

	/* BOTTOM { */

	.acf-table-bottom-row {
		position: relative;
		display: table-row;
	}

	.acf-table-bottom-left {
		position: relative;
		display: table-cell;
		height: 24px;
	}

	.acf-table-bottom-cell {
		position: relative;
		display: table-cell;
	}

	.acf-table-bottom-right {
		position: relative;
		display: table-cell;
	}

	/* BOTTOM } */

	/* HIDES { */

	/* .acf-table-hide-header { */

	.acf-table-hide-header .acf-table-header-row,
	.acf-table-hide-header .acf-table-header-row * {
		height: 0 !important;
		font-size: 0 !important;
		line-height: 0 !important;
		padding: 0 !important;
		border: 0 !important;
		display: block;
	}

	.acf-table-hide-header .acf-table-top-row>* {
		border-bottom: none;
	}

	/* .acf-table-hide-header } */

	/* .acf-table-hide-left { */

	.acf-table.acf-table-hide-left>*>*:first-child {
		font-size: 0 !important;
		line-height: 0 !important;
		width: 16px !important;
		padding: 0 !important;
		/*border: 0 !important;*/
		background: none !important;
		border-left: none;
	}

	/* .acf-table-hide-left } */

	/* .acf-table-hide-top { */

	.acf-table.acf-table-hide-top .acf-table-top-left,
	.acf-table.acf-table-hide-top .acf-table-top-cell,
	.acf-table.acf-table-hide-top .acf-table-top-right {
		height: 0 !important;
		font-size: 0 !important;
		line-height: 0 !important;
		height: 16px !important;
		/*padding: 0 !important;
                border: 0 !important;*/
		background: none !important;
		border-top: none;
	}

	/* .acf-table-hide-top } */

	/* HIDE } */

	/* TABLE } */

	/* BUTTONS { */

	.acf-table-add-col {
		box-sizing: content-box;
		position: absolute !important;
		top: -9px;
		right: -10px;
		z-index: 1;
	}

	.acf-table-add-row {
		box-sizing: content-box;
		position: absolute !important;
		bottom: -9px;
		left: -10px;
		z-index: 1;
	}

	.acf-table-remove-col {
		box-sizing: content-box;
		position: absolute !important;
		top: 4px;
		left: 50%;
		margin-left: -10px;
	}

	.acf-table-remove-row {
		box-sizing: content-box;
		position: absolute !important;
		left: 5px;
		top: 50%;
		transform: translateY(-50%);
	}

	.acf-table-add-link {
		margin: 0 auto;
	}

	/* BUTTONS } */

	/* CELL EDITOR { */

	.acf-table-cell-editor {
		display: none;
		z-index: 2;
		border: 2px solid var(--dpu-color-3);
		border-radius: 5px;
		font-size: 0;
		line-height: 0;

	}

	.acf-table-cell-editor-textarea {
		border: none !important;
		padding: 7px 7px !important;
		background-color: rgba(var(--dpu-color-3), 0.05);
		color: rgba($text-color-dark, 1);
		font-size: 13px;
		line-height: 1.4;

		/* fix profile.php ACF v4 */
		width: 100% !important;
		margin-bottom: 0 !important;
		height: auto;
	}


	/* CELL EDITOR } */

	/* OPTIONBOX { */

	.acf-table-optionwrap {
		margin-top: 16px;
		margin-bottom: 16px;
		padding: 0 51px 0 45px;
	}

	.acf-table-optionbox {
		margin-top: 16px;
		margin-bottom: 16px;
	}

	.acf-table-optionbox+.acf-table-optionbox {
		margin-top: -20px;
	}

	/* FIELDS { */

	.acf-table-optionbox label {
		display: inline-block;
		font-weight: bold;
		padding-right: 6px;
		padding-bottom: 10px;
		padding-top: 10px;
	}

	.acf-table-fc-opt-use-header {
		width: auto !important;
		padding: 3px 24px 3px 8px !important;
		/* fixes ACF padding 2px for select fields */
	}

	.acf-table-fc-opt-caption {
		width: 100% !important;
	}

	/* FIELDS } */

	/* OPTIONBOX } */

	/*{ ADJUSMENTS*/



	.acf-table-wrap {
		padding-left: 10px;
		padding-right: 10px;
	}





	.dp-hidden {
		display: none !important;
	}

	.acf-table-remove-col,
	.acf-table-add-col {
		display: none !important;
	}



	.acf-table-top-row {
		display: none;
	}

	.acf-table-body-cont--select>.acf-table-body-cont-sub {
		min-width: 80px;
	}

	.acf-table-body-cell--link-multi li,
	.acf-table-body-cell--link:not(.acf-table-body-cell--link-multi) {
		cursor: pointer;
	}

	.acf-table-body-cell--link-all-pages {
		pointer-events: none;
		position: relative;

		.acf-table-body-cont {
			display: none;
		}

		&::after {
			position: absolute;
			display: flex;
			justify-content: center;
			align-items: center;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			content: '$ALL';
			background-color: rgba(var(--dpu-color-3), 0.351);
			color: rgba(var(--dpu-color-3-text), 1);
			font-weight: 700;
		}
	}


	// touch devices
	@media (hover: none) {


		.acf-table-body-left {
			min-width: 50px;
		}

		.acf-table-body-cont--select>.acf-table-body-cont-sub {
			height: 40px;
		}

		.acf-table-wrap {
			padding: 11px 36px 11px 20px;

			.acf-table-add-row {
				left: -19px;
				bottom: -19px;
			}
		}

	}

	.acf-field-table .acf-table-wrap {
		overflow-x: auto;
		overflow-y: hidden;
		-webkit-overflow-scrolling: touch;
	}
}


/*ADJUSMENTS }*/